	module.exports = {"pathContext":{"docHtml":"<h1>triple-beam</h1>\n<p>Definitions of levels for logging purposes &#x26; shareable Symbol constants.</p>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">const { LEVEL } = require('triple-beam');\nconst colors = require('colors/safe');\n\nconst info = {\n  [LEVEL]: 'error',\n  level: 'error',\n  message: 'hey a logging message!'\n};\n\n// Colorize your log level!\ninfo.level = colors.green(info.level);\n\n// And still have an unmutated copy of your level!\nconsole.log(info.level === 'error');  // false\nconsole.log(info[LEVEL] === 'error'); // true\n</code></pre>\n<h2>Tests</h2>\n<p>Tests are written with <code>mocha</code>, <code>assume</code>, and <code>nyc</code>. They can be run with <code>npm</code>:</p>\n<pre><code>npm test\n</code></pre>\n<h5>LICENSE: MIT</h5>\n<h5>AUTHOR: <a href=\"https://github.com/indexzero\">Charlie Robbins</a></h5>","docPath":"argo-ci/node_modules/triple-beam/readme","proj":"argo-ci"}}

